export const rules = {
  validPwd: (rule, value, callback) => {
    if (!value) {
      callback(new Error(''))
    } else if (!(/^(?![^a-zA-Z]+$)(?!\D+$)/.test(value))) {
      callback(new Error(''))
    } else {
      callback()
    }
  },
  email: (rule, value, callback) => {
    const reg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
    if (!reg.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
  },
  moblie: (rule, value, callback) => {
    if (!(/^1[34578]\d{9}$/.test(value))) {
      callback(new Error(''))
    } else {
      callback()
    }
  },
  checkTel (rule, value, callback) {
    if ((/^1[34578]\d{9}$/.test(value)) || /^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/.test(value)) {
      callback()
    } else {
      callback(new Error(''))
    }
  },
  number: (rule, value, callback) => {
    const reg = /^[0-9]*$/
    if (!reg.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
  },
  auth: (rule, value, callback) => {
    if (JSON.stringify(value) === '{}' || (value.menuLists && value.menuLists.length === 0)) {
      callback(new Error(''))
    } else {
      callback()
    }
  },
  string: (rule, value, callback) => {
    if (!value.replace(/^(\s*)|(\s*)$/g, '')) {
      callback(new Error(''))
    } else {
      callback()
    }
  },
  username: (rule, value, callback) => {
    const reg = /^[\u4e00-\u9fa5_a-zA-Z0-9.]+$/
    if (!reg.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
  },
  time: (rule, value, callback) => {
    const nowTime = new Date().getTime()
    const currentTime = new Date(value).getTime()
    if (!value) {
      callback(new Error(''))
    }
    if (currentTime < nowTime) {
      callback(new Error(''))
    } else {
      callback()
    }
  },
  timerange: (rule, value, callback) => {
    const nowTime = new Date().getTime()
    const startTime = new Date(value[0]).getTime()
    const endTime = new Date(value[1]).getTime()
    if (!value) {
      callback(new Error(''))
    }
    if (startTime < nowTime || endTime < nowTime) {
      callback(new Error(''))
    } else {
      callback()
    }
  },
  year: (rule, value, callback) => {
    const nowTime = new Date().getFullYear()
    if (!value) {
      callback(new Error(''))
    }
    if (value < nowTime) {
      callback(new Error(''))
    } else {
      callback()
    }
  },
  name: (rule, value, callback) => {
    const reg = /^[\w\u4E00-\u9FA5\uF900-\uFA2D]*$/
    if (!reg.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
  },
  idCard (rule, value, callback) {
    const reg = new RegExp(/^(\d{15}$|^\d{18}$|^\d{17}(\d|X|x))$/)
    if (!reg.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
  }
}
