<template>
  <div>
    <!--
      editorkeytinymce keep-alivetinymce
      activatedkey
    -->
    <editor id="tinymceEditor" :init="tinymceInit" v-model="content" :key="tinymceFlag"></editor>
    <el-upload
      :action="uploadUrl"
      :on-success="insertImage"
      multiple
      style="display: none">
      <el-button ref="imageUpload"></el-button>
    </el-upload>
<!--    <span v-html="content"></span>-->
  </div>
</template>
<script>
import tinymce from 'tinymce/tinymce'
import 'tinymce/themes/silver/theme'
import Editor from '@tinymce/tinymce-vue'

import 'tinymce/plugins/textcolor'
import 'tinymce/plugins/advlist'
import 'tinymce/plugins/table'
import 'tinymce/plugins/lists'
import 'tinymce/plugins/paste'
import 'tinymce/plugins/preview'
import 'tinymce/plugins/fullscreen'

export default {
  name: 'TinymceEditor',
  components: {
    'editor': Editor
  },
  props: {
    data: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      tinymceFlag: 1,
      tinymceInit: {},
      content: this.data ? this.data : '',
      uploadUrl: '/uploadImg'
    }
  },
  watch: {
    content () {
      this.$emit('textChange', this.content)
    }
  },
  methods: {
    // 
    insertImage (res, file) {
      let src = res.data.url // 
      tinymce.execCommand('mceInsertContent', false, `<img src=${src}>`)
    }
  },
  created () {
    this.uploadUrl = this.baseUrl + this.uploadUrl
    const that = this
    this.tinymceInit = {
      skin_url: '/tinymce/skins/ui/oxide',
      language_url: `/tinymce/langs/zh_CN.js`,
      language: 'zh_CN',
      height: document.body.offsetHeight - 300,
      browser_spellcheck: true, // 
      branding: false, // 
      // elementpath: false,  //
      statusbar: false, // 
      paste_data_images: true, // 
      menubar: false, // menu
      plugins: 'advlist table lists paste preview fullscreen',
      toolbar: 'fontselect fontsizeselect forecolor backcolor bold italic underline strikethrough | alignleft aligncenter alignright alignjustify | imageUpload quicklink h2 h3 blockquote table numlist bullist preview fullscreen',
      /**
       * tinymce
       * iviewUpload,
       */
      setup: (editor) => {
        editor.ui.registry.addButton('imageUpload', {
          // text: '',
          tooltip: '',
          icon: 'image',
          onAction: () => {
            let upload = that.$refs.imageUpload
            console.log(upload)
            upload.$el.click()
          }
        })
      }
    }
  },
  activated () {
    this.tinymceFlag++
  },
  mounted () {

  }
}
</script>

<style>
  .tox-silver-sink{
    z-index: 9999 !important;
  }
</style>
